@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/montserratbold.woff2") format("woff2"), url("../fonts/montserratbold.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/montserratsemibold.woff2") format("woff2"), url("../fonts/montserratsemibold.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/montserratmedium.woff2") format("woff2"), url("../fonts/montserratmedium.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/montserratregular.woff2") format("woff2"), url("../fonts/montserratregular.woff") format("woff");
  font-display: swap; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0 auto;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  color: #011c40;
  background-color: #f9fbfd; }
  @media (max-width: 1023px) {
    body {
      max-width: 1023px; } }
  @media (max-width: 767px) {
    body {
      max-width: 767px; } }

img {
  max-width: 100%;
  object-fit: cover; }

a {
  text-decoration: none;
  color: inherit; }

.button {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  color: #011c40;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer; }

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.page-main {
  flex-grow: 1; }

.page-header {
  position: relative;
  padding: 35px 103px 268px;
  color: #f9fbfd;
  background-color: #255eac;
  background-image: url("../../img/background/main-screen-desktop.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); }
  @media (min-resolution: 2dppx) {
    .page-header {
      background-image: url("../../img/background/main-screen-desktop@2x.jpg"); } }
  @media (max-width: 1023px) {
    .page-header {
      padding: 27px 45px 266px;
      background-image: url("../../img/background/main-screen-tablet.jpg");
      box-shadow: none; } }
  @media (max-width: 1023px) and (min-resolution: 2dppx) {
    .page-header {
      background-image: url("../../img/background/main-screen-tablet@2x.jpg"); } }
  @media (max-width: 767px) {
    .page-header {
      padding: 20px 15px 170px;
      background-image: url("../../img/background/main-screen-mobile.jpg");
      min-height: 500px; } }
  @media (max-width: 767px) and (min-resolution: 2dppx) {
    .page-header {
      background-image: url("../../img/background/main-screen-mobile@2x.jpg"); } }

.page-header__container {
  margin-bottom: 195px;
  display: flex;
  justify-content: space-between; }
  @media (max-width: 1023px) {
    .page-header__container {
      margin-bottom: 215px; } }
  @media (max-width: 767px) {
    .page-header__container {
      margin-bottom: 128px; } }

@media (max-width: 1023px) {
  .page-header__logo svg {
    width: 38px;
    height: 20px; } }

@media (max-width: 767px) {
  .page-header__logo svg {
    width: 42px;
    height: 22px; } }

.page-header__navigation {
  display: flex;
  align-items: center; }

.page-header__toggle {
  display: none; }
  @media (max-width: 767px) {
    .page-header__toggle {
      display: block;
      width: 24px;
      height: 24px;
      background-color: transparent;
      border: none;
      cursor: pointer;
      position: absolute;
      top: 20px;
      right: 15px; }
      .page-header__toggle::before {
        position: absolute;
        content: "";
        width: 24px;
        height: 2px;
        top: 4px;
        left: 0;
        right: 0;
        background-color: #f9fbfd;
        box-shadow: 0 6px 0 0 #f9fbfd, 0 12px 0 0 #f9fbfd; } }

.page-header__list {
  display: flex;
  flex-wrap: wrap;
  font-size: 16px;
  line-height: 20px;
  font-weight: 500; }
  @media (max-width: 1023px) {
    .page-header__list {
      font-size: 14px;
      line-height: 17px; } }
  @media (max-width: 767px) {
    .page-header__list {
      display: none; } }

.page-header__list li {
  margin: 5px 0 5px 33px; }
  @media (max-width: 1023px) {
    .page-header__list li {
      margin: 5px 0 5px 23px; } }

@media (max-width: 767px) {
  .page-header.active .page-header__container {
    align-items: start;
    margin: 0;
    padding-top: 10px;
    padding-left: 5px;
    position: absolute;
    min-width: calc(100% - 35px);
    top: 0;
    right: 0;
    min-height: 568px;
    background-color: #f9fbfd; }
  .page-header.active svg {
    fill: #011c40; }
  .page-header.active .page-header__toggle::before, .page-header.active .page-header__toggle::after {
    position: absolute;
    content: "";
    width: 16px;
    height: 2px;
    top: 10px;
    right: 0;
    background-color: #011c40; }
  .page-header.active .page-header__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
    left: calc(50% - 4px); }
  .page-header.active .page-header__toggle::after {
    transform: rotate(-45deg); }
  .page-header.active .page-header__list {
    display: block;
    position: absolute;
    top: 75px;
    left: 0;
    right: 0;
    font-size: 18px;
    line-height: 22px;
    background-color: #f9fbfd; }
  .page-header.active li {
    margin: 0 0 8px;
    padding-left: 15px; }
  .page-header.active a {
    display: block;
    padding: 10px;
    color: #011c40; }
  .page-header.active h1,
  .page-header.active p {
    display: none; } }

.page-header__wrapper {
  padding: 0 95px; }
  @media (max-width: 1023px) {
    .page-header__wrapper {
      padding: 0; } }

.page-header h1 {
  margin: 0 0 10px;
  font-size: 54px;
  line-height: 66px;
  font-weight: 700; }
  @media (max-width: 1023px) {
    .page-header h1 {
      font-size: 50px;
      line-height: 61px; } }
  @media (max-width: 767px) {
    .page-header h1 {
      font-size: 36px;
      line-height: 45px; } }

.page-header p {
  margin: 0 0 10px;
  font-size: 20px;
  line-height: 24px; }
  @media (max-width: 1023px) {
    .page-header p {
      font-size: 18px;
      line-height: 22px; } }
  @media (max-width: 767px) {
    .page-header p {
      font-size: 16px; } }

.page-footer {
  margin-top: auto;
  padding: 50px 96px;
  display: flex;
  justify-content: space-between;
  color: #f9fbfd;
  background-color: #011c40; }
  @media (max-width: 1023px) {
    .page-footer {
      padding: 45px 39px 45px 43px; } }
  @media (max-width: 767px) {
    .page-footer {
      padding: 40px 15px 34px;
      display: block; } }

.page-footer__container {
  display: flex;
  align-items: center; }
  @media (max-width: 767px) {
    .page-footer__container {
      display: block; } }

.page-footer__logo {
  margin: 0 7px; }
  @media (max-width: 1023px) {
    .page-footer__logo {
      margin: 0 2px; } }
  @media (max-width: 767px) {
    .page-footer__logo {
      display: block;
      margin: 0 0 16px; } }

@media (max-width: 1023px) {
  .page-footer__logo img {
    width: 38px;
    height: 20px; } }

@media (max-width: 767px) {
  .page-footer__logo img {
    width: 42px;
    height: 22px; } }

.page-footer__navigation {
  display: flex;
  flex-wrap: wrap; }
  @media (max-width: 767px) {
    .page-footer__navigation {
      display: block;
      margin-bottom: 20px; } }

.page-footer__navigation li {
  margin-left: 33px; }
  @media (max-width: 1023px) {
    .page-footer__navigation li {
      margin-left: 23px; } }
  @media (max-width: 767px) {
    .page-footer__navigation li {
      margin: 0 0 7px; } }

.page-footer__navigation a {
  line-height: 20px;
  font-weight: 500; }
  @media (max-width: 1023px) {
    .page-footer__navigation a {
      font-size: 12px;
      line-height: 15px; } }

.page-footer__social-list {
  display: flex;
  align-items: center;
  flex-wrap: wrap; }
  @media (max-width: 767px) {
    .page-footer__social-list {
      margin-left: -9px; } }

.page-footer__social-list li {
  margin-left: 14px; }
  @media (max-width: 1023px) {
    .page-footer__social-list li {
      margin-left: 9px; } }
  @media (max-width: 767px) {
    .page-footer__social-list li {
      margin: 0 9px 0 0; } }

.page-footer__social-list a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 34px;
  height: 34px; }
  @media (max-width: 1023px) {
    .page-footer__social-list a {
      width: 28px;
      height: 28px; } }

@media (max-width: 1023px) {
  .page-footer__social-list svg {
    width: 16px;
    height: 16px; } }

.description {
  padding: 120px 103px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  align-items: center; }
  @media (max-width: 1023px) {
    .description {
      padding: 91px 45px 100px;
      gap: 30px;
      align-items: flex-start; } }
  @media (max-width: 767px) {
    .description {
      display: block;
      padding: 56px 15px 65px; } }

.description h2 {
  margin: 0 0 25px;
  font-size: 28px;
  line-height: 42px;
  font-weight: 600; }
  @media (max-width: 1023px) {
    .description h2 {
      margin: 0 0 16px;
      font-size: 26px;
      line-height: 36px; } }
  @media (max-width: 767px) {
    .description h2 {
      margin: 0 0 13px;
      font-size: 24px;
      line-height: 34px; } }

.description p {
  margin: 0 0 10px; }
  @media (max-width: 1023px) {
    .description p {
      margin: 0 0 35px; } }

@media (max-width: 767px) {
  .description br {
    display: none; } }

.description__container {
  position: relative;
  max-width: 560px;
  height: 301px;
  overflow: hidden; }
  @media (max-width: 1023px) {
    .description__container {
      margin-top: 9px;
      max-width: 324px;
      height: 264px; } }
  @media (max-width: 767px) {
    .description__container {
      max-width: 290px;
      height: 186px; } }

.description img {
  position: absolute;
  top: -37px;
  left: -102px;
  max-width: none; }
  @media (max-width: 1023px) {
    .description img {
      top: 0;
      left: -180px;
      height: 100%;
      object-fit: contain; } }
  @media (max-width: 767px) {
    .description img {
      left: -198px; } }

